html, body, #root {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    width: 100%;
    height: 100%;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.chat {
    background-color: rgba(24, 27, 34, 0.46);
    border: solid #235fbb 1px;
    padding: 2rem;
    box-sizing: border-box;

    display: flex;
    flex-flow: column;

    width: 100vmin;
    height: 90vmin;
}

.chat hr {
    color: #7e99a0;
    width: 100%;
}

.chat .header {
    color: #6ca7bb;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.chat h3 {
    margin-bottom: 0;
}

form.message-form {
    width: 100%;
    display: flex;
    justify-content: center;
    align-self: end;
    margin-bottom: 1rem;
}



.chat ul {
    list-style: none;
    scrollbar-color: #4c7685 #3A4252;
}

.chat ul::-webkit-scrollbar {
    width: 0.5rem;
    background-color: #3A4252;
}

.chat ul::-webkit-scrollbar-thumb {
    background-color: #4c7685;
}

.list-message {
    display: flex;
    flex-flow: column;
    padding: 0 0.5rem 0 0;
    flex-grow: 1;
    margin: 0 0 0.5rem 0;
    overflow-y: auto;
    align-items: flex-end;
}


li.message {
    margin-bottom: 5px;
    flex-direction: column;
    background-color: rgba(74, 92, 99, 0.36);
    padding: 0.5rem 1rem;
    color: #6ca7bb;
    min-width: 40%;
    max-width: 60%;
    display: flex;
    flex-flow: column;
}

.message pre {
    white-space: pre-wrap; /* css-3 */
    white-space: -moz-pre-wrap; /* Mozilla, since 1999 */
    white-space: -o-pre-wrap; /* Opera 7 */
    word-wrap: break-word; /* Internet Explorer 5.5+ */
}

.message h4 {
    margin: 0;
}

.message p {
    font-size: 0.8rem;
    margin: 0.5rem 0;
    color: lightgrey;
}

.message small {
    font-size: 0.7rem;
    color: darkgrey;
    margin-top: 1rem;
}

.error-message{
    position: relative;
    top: 1rem;
    left: 0;
    background-color: darkred;
    color: wheat;
}

@media all and (max-width: 1024px) {
    .chat {
        width: 100vw;
        max-width: none;
        height: 100vh;
        max-height: none;
        padding: 1rem 1rem 2rem 1rem;
    }

    .chat input,
    .chat .button {
        padding: 0.5rem 1rem
    }
}

.app {
    background-color: #282c34;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.container {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.logo-background {
    pointer-events: none;
    width: 50%;
    height: 50%;
    margin: auto auto;
}

.logo-spin {
    -webkit-animation: spin-frames infinite 20s linear;
            animation: spin-frames infinite 20s linear;
}

@-webkit-keyframes spin-frames {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes spin-frames {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.center {
    align-self: center;
}

.w-50 {
    width: 50%;
}

.w-100 {
    width: 100%;
}

.mb-1 {
    margin-bottom: 1rem;
}

textarea,
input {
    background-color: rgba(74, 92, 99, 0.36);
    color: #6ca7bb;
    border: solid #235fbb 1px;
    flex-grow: 2;
    height: auto;
}

textarea {
    padding: 0.2rem 0.5rem;
}

input{
    padding: 1rem
}


textarea:focus,
input:focus {
    box-shadow: 0 0 1px 1px #235fbb;
}

.button {
    background-color: rgba(74, 92, 99, 0.36);
    color: #6ca7bb;
    border: solid #235fbb 1px;
    padding: 1rem;
    cursor: pointer;
}
.button-sm{
    padding: 0.5rem 1rem;
}

.float-left {
    align-self: flex-start;
}

.float-right {
    align-self: flex-end;
}

